"use strict";

// 下拉菜单和购物车
$(function () {
  var hide_timer = null;
  var flag = true;
  $("#shoplist").mousemove(function () {
    flag = false;
    $(".shop-list").css({
      opacity: 1
    });
    $(this).css({
      cursor: "pointer"
    });
  }); //定时器

  $("#shoplist").mouseout(function () {
    flag = true;
    clearInterval(hide_timer); // $(".shop-list").css({
    //   opacity: 0,
    // });

    hide_timer = setInterval(function () {
      $(".shop-list").css({
        opacity: 0
      });
    }, 100);
  });
  $(".shop-list").mousemove(function () {
    clearTimeout(hide_timer);
  });
  $(".shop-list").mouseout(function () {
    clearInterval(hide_timer);
    hide_timer = setInterval(function () {
      $(".shop-list").css({
        opacity: 0
      });
    }, 200);
  });
}); //banner

var swiper = new Swiper('.swiper-container', {
  spaceBetween: 30,
  effect: 'fade',
  fadeEffect: {
    crossFade: true
  },
  pagination: {
    el: '.swiper-pagination',
    clickable: true
  },
  autoplay: {
    delay: 4000,
    stopOnLastSlide: false,
    disableOnInteraction: true
  }
}); // 渲染界面

function load() {
  var options = {
    url: "http://localhost:3000/shot_list",
    success: render,
    dataType: "json"
  };
  ajax(options);
}

function render(res) {
  // console.log(res)
  var data = res.data; // console.log(data)

  var html = ""; // console.log(data)

  data.forEach(function (item) {
    // console.log(item)
    html += "\n     <div class=\"hot-list-duct\">\n    <figure class=\"item-cover\">\n      <img\n        src=\"".concat(item.image_url, "\"\n        lazy=\"loaded\"\n      />\n    </figure>\n    <article class=\"artivle\">\n      <h3>").concat(item.goods_name, "</h3>\n      <h5 class=\"txt-product-title\">\n       ").concat(item.short_name, "\n      </h5>\n    </article>\n    <aside class=\"item-attr-colors\">\n      <figure class=\"outer\">\n        <img\n          data-v-380b3c68=\"\"\n          src=\"https://resource.smartisan.com/resource/a95138cf41c70fbb689e0bcd785a362f.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n      <figure class=\"outer active\">\n        <img\n          src=\"https://resource.smartisan.com/resource/b86e1a8858c1445d3fd77a0dcba2eb5c.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n      <figure class=\"outer\">\n        <img\n          data-v-380b3c68=\"\"\n          src=\"https://resource.smartisan.com/resource/d0c2d694d76184aa8deabab5e8e951af.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n    </aside>\n    \n    <article class=\"item-price\">\n      <span>\uFFE5").concat(item.nowsales.toFixed(2), "</span>\n      <span class=\"orignal-price\">\uFFE5").concat(item.befsales.toFixed(2), "</span>\n    </article>\n  </div>");
  });
  _("#hot-list").innerHTML = html;
}

load();

function productlist() {
  var options = {
    url: "http://localhost:3000/productlis",
    success: renderproductlist,
    dataType: "json"
  };
  ajax(options);
}

function renderproductlist(res) {
  var data = res.data.advimg;
  var list = res.list.data; // console.log(data, list)

  var html = ""; // console.log(data)

  data.forEach(function (item) {
    // console.log(item)
    // console.log(data.adv_url)
    html += "\n    <div class=\"advertise-img\">\n    <img\n      src=\"".concat(item.advimg_url, "\"\n      alt=\"\"\n    />\n  </div>");
  });
  list.forEach(function (item) {
    // console.log(item)
    html += "\n  <div class=\"list-duct\">\n    <figure class=\"item-cover\">\n      <img\n        src=\"".concat(item.image_url, "\"\n        lazy=\"loaded\"\n      />\n    </figure>\n    <article class=\"artivle\">\n      <h3>").concat(item.goods_name, "</h3>\n      <h5 class=\"txt-product-title\">\n       ").concat(item.short_name, "\n      </h5>\n    </article>\n    <aside class=\"item-attr-colors\">\n      <figure class=\"outer\">\n        <img\n          data-v-380b3c68=\"\"\n          src=\"https://resource.smartisan.com/resource/a95138cf41c70fbb689e0bcd785a362f.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n      <figure class=\"outer active\">\n        <img\n          src=\"https://resource.smartisan.com/resource/b86e1a8858c1445d3fd77a0dcba2eb5c.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n      <figure class=\"outer\">\n        <img\n          data-v-380b3c68=\"\"\n          src=\"https://resource.smartisan.com/resource/d0c2d694d76184aa8deabab5e8e951af.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n    </aside>\n    <article class=\"item-price\">\n      <span>\uFFE5").concat(item.nowsales.toFixed(2), "</span>\n      <span class=\"orignal-price\">\uFFE5").concat(item.befsales.toFixed(2), "</span>\n    </article>\n  </div>");
  }); // console.log(html)

  _("#prduct-list").innerHTML = html;
}

productlist();

